import { defineComponent } from 'vue';
import { init } from '../../ctx';
import ProgressBar from '../common/ProgressBar.vue';
export default defineComponent({
    components: {
        ProgressBar,
    },
    props: {
        id: {
            default: 0,
            type: Number,
        },
        peilingId: {
            default: 0,
            type: Number,
        },
        titel: {
            default: '',
            type: String,
        },
        beschrijving: {
            default: '',
            type: String,
        },
        stemmen: {
            default: 0,
            type: Number,
        },
        magStemmen: Boolean,
        aantalGestemd: {
            default: 0,
            type: Number,
        },
        heeftGestemd: Boolean,
        keuzesOver: Boolean,
        modelValue: Boolean,
    },
    emits: ['update:modelValue'],
    computed: {
        kanStemmen() {
            return this.magStemmen && !this.heeftGestemd;
        },
        progress() {
            return ((this.stemmen / this.aantalGestemd) * 100).toFixed(2);
        },
        progressText() {
            return `${this.progress}% (${this.stemmen})`;
        },
        isDisabled() {
            return !this.modelValue && !this.keuzesOver;
        },
    },
    watch: {
        kanStemmen() {
            this.initBeschrijvingContext();
        },
    },
    mounted() {
        this.initBeschrijvingContext();
    },
    methods: {
        initBeschrijvingContext() {
            setTimeout(() => {
                if (this.kanStemmen) {
                    init(this.$refs.beschrijving);
                }
                else {
                    init(this.$refs.beschrijving_gestemd);
                }
            });
        },
    },
});
